





















import Vue from 'vue'
import { Component, Prop } from 'vue-property-decorator'
import { VCheckbox } from 'vuetify/lib'

@Component({
  components: { VCheckbox }
})
export default class TodoItemView extends Vue {
  @Prop({
    type: Object,
    required: true
  })
  node

  @Prop({
    type: Object,
    required: true
  })
  view

  @Prop({
    type: Function,
    required: true
  })
  updateAttrs: (...args: any) => any

  onChange () {
    this.updateAttrs({
      done: !this.node.attrs.done
    })
  }
}
